// В этом файле должны быть стили для БЭМ-блока navigation-mobile, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.navigation-mobile {
  display: block;
  min-width: 320px;
  font-size: 14px;
  margin: 20px 0 0;
  color: $gray-dark;
  text-transform: uppercase;
  margin: 0 auto;

  &__togglers-wrapper {
    font-weight: bold;
    display: flex;
    flex-flow: row wrap;
    border-top: 1px solid #eec00a;
    border-bottom: 1px solid #eec00a;
  }

  &__toggler {
    position: relative;
    cursor: pointer;
    text-align: center;
    width: 50%;

    &--categories {
      padding: 15px 25px 15px 15px;
      display: inline-block;
      width: 100%;
      transition: $transition-time;

      &:hover {
        background: #ffefa6;
      }

      &:after {
        content: "";
        background: url(../img/open-exit-door.svg);
        background-size: 18px 18px;
        width: 18px;
        height: 18px;
        position: absolute;
        margin-left: 10px;
        top: 13px;
      }

    }

    &--menu {
      padding: 15px 25px 15px 15px;
      display: inline-block;
      width: 100%;
      transition: $transition-time;

      &:hover {
        background: #ffefa6;
      }

      &:after {
        content: "";
        background: url(../img/menu_icon.svg);
        background-size: 18px 18px;
        width: 18px;
        height: 18px;
        position: absolute;
        margin-left: 10px;
        top: 13px;
      }

    }

  }

  &__categories,
  &__menu {
    display: flex;
    flex-direction: column;
    text-align: center;
    height: auto;
    opacity: 1;
    visibility: visible;
    transition: 0.5s;

    &-item {

      & a {
        display: block;
        height: 100%;
        width: 100%;
        padding: 15px 0;
        position: relative;
        cursor: pointer;
      }

      &--actions a:before {
        content: "";
        display: inline-block;
        background: url(../img/a_icon.svg) no-repeat;
        background-size: 12px;
        width: 12px;
        height: 12px;
        position: relative;
        left: -15px;
        vertical-align: middle;
        transform: scale(2);
      }

      &--hasmenu > a:after {
        content: "";
        display: inline-block;
        position: absolute;
        background: url(../img/arrow-down.svg);
        background-size: 12px 12px;
        width: 12px;
        height: 12px;
        right: 45px;
        top: 16px;
        transition: $transition-time;
      }
      &--hasmenu > a.active:after {
        content: "";
        display: inline-block;
        position: absolute;
        background: url(../img/arrow-down.svg);
        transform: rotate3d(1, 0, 0, 180deg);
        background-size: 12px 12px;
        width: 12px;
        height: 12px;
        right: 45px;
        top: 16px;
      }

      & a:hover {
        background: #ffefa6;
      }
    }
  }

  &__categories--hidden, &__menu--hidden {
    height: 0;
    visibility: hidden;
    overflow: hidden;
  }

  &__subcategories-item {
    background: #f7f7f7;
    border-bottom: 1px solid #e1e0e0;
    font-size: 12px;
  }

}

@media (min-width: 769px) {

  .navigation-mobile {
    display: none;
  }

}