// В этом файле должны быть стили для БЭМ-блока cart, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.cart {
  width: 100%;
  min-height: 50vh;
  
  &__table {
    display: table;
    border-collapse: collapse;
    width: 100%;
  }

  &__error-text {
    line-height: 1.3;
  }

  &__error-text a {
    border-bottom: 1px solid;
    &:hover {
      color: $red;
    }
  }

  &__title {
    font-size: 29px;
    margin: 25px 0;
  }

  &__table-header {
    display: table-row;
    vertical-align: middle;

    &-cell {
      display: table-cell;
      padding: 14px;
      border: 1px solid $gray-light;
      background: #fbfbfb;
      min-width: 110px;
    }
  }

  &__summary-line {
    text-align: right;
    margin: 20px 16px;
    font-size: 20px;
  }

  &__summary-price {
    color: $red;
    font-size: 20px;
    font-weight: normal;
    white-space: nowrap;
  }

  &__buttons {
    text-align: right;
    margin: 10px 0 20px;

    .button {
      font-size: 17px;
      font-weight: normal;

      &:after {
        height: 38px;
      }
    }
  }
}

@media (max-width: $screen-lg) {
  .cart {
    overflow-x: hidden;
  }
  .cart__table {
    display: flex;
    position: relative;
    padding-left: 100px;
  }
  .cart__table::after {
    content: '';
    background: url(../img/cart-mobile-arrow.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    display: inline-block;
    position: absolute;
    top: -52px;
    right: 0;
    animation: opacity-blink 3s linear infinite;
  }
  .cart__table-header {
    display: flex;
    flex-direction: column;
    min-width: 100px;
    max-width: 100px;
    position: absolute;
    top: 0;
    left: 0;
    & {
      display: flex;
      flex-direction: column;
    }

    &-cell:first-child {
      height: 130px;
    }

    &-cell:nth-child(2) {
      height: 68px;
    }

    &-cell:nth-child(3) {
      height: 74px;
    }

    &-cell:nth-child(4), &-cell:nth-child(5) {
      height: 46px;
    }

    &-cell:nth-child(6) {
      height: 50px;
    }
  }
  .cart__items-carousel {
    display: flex;
    overflow-x: scroll;
  }
  .cart__table-header-cell:not(:last-child) {
    border-bottom: none;
  }
  .cart__refresh.button {
    font-size: 0;
    background-image: url(../img/cart-refresh-button.svg);
    background-size: 25px;
    background-position: center;
    background-repeat: no-repeat;
    width: 20px;
    min-height: 38px;
    min-width: 0;
    display: inline-block;
    vertical-align: middle; 
  }
  .cart__summary-line {
    margin: 20px 5px;
  }
}

@keyframes opacity-blink {
  from {
    opacity: 0;
    right: 30px;
  }
  to {
    opacity: 1;
  }
}