// В этом файле должны быть стили для БЭМ-блока pagination, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.pagination {
  font-size: 14px;
  text-align: center;
  margin: 0 auto;

  & a {
    color: $gray-mutted;
    transition: $transition-time;
  }

  &__button {
    background: $white;
    border: 1px solid $gray-mutted;
    border-radius: 20px;
    display: inline-block;
    transition: $transition-time;
    font-family: Arial, sans-serif;

    & a {
      padding: 5px 10px;
      display: inline-block;
    }

    &:hover {
      background: $red;
      border: 1px solid $red;
      cursor: pointer;
    }

    &:hover a {
      color: $white;
    }

    &.pagination__button--active {
      margin-left: 4px;
      margin-right: 4px;
      font-weight: 700;
      text-decoration: none;
      font-size: 14px;
      background: $red;
      border: 1px solid $red;
    }

    &.pagination__button--active a {
      color: $white;
    }
  }
}

@media (max-width: 480px) {
  .pagination__button--to-start,
  .pagination__button--to-end {
    display: none;
  }
}

@media (max-width: 1020px) {
  .pagination__button {
    margin-bottom: 15px;
  }
}