// В этом файле должны быть стили для БЭМ-блока tabs, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.tabs {
    margin-top: 20px;
}

.tabs__buttons-wrapper {
  display: flex;
  border-bottom: 1px solid $gray-light;
  margin-bottom: 20px;
}

.tabs__button--link {
  background: none;
  border: none;
  padding: 0;
}

.tabs__button,
.tabs__button--link {
  margin-right: 4px;
  background: linear-gradient(to bottom, $white 60%, rgba(226, 226, 226, 0.5) 100%);
  border: 1px solid $gray-light;
  padding: 7px 15px;
  font-size: 14px;
  position: relative;
  bottom: -1px;
  cursor: pointer;
  transition: $transition-time;
}

.tabs__button:hover {
  color: $red;
}

.tabs__button:focus {
  outline: none;
}

.tabs__button.tabs__button--active {
  color: $link-hover-color;
  cursor: default;
}

.tabs__button.tabs__button--active span {
  padding-bottom: 2px;
  border-bottom: 1px dashed;
}

.tabs__button.tabs__button--active::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 8px solid rgba(226, 226, 226, 0.6);
}

.tabs__content {
  visibility: hidden;
  opacity: 0;
  height: 0;
  width: 100%;
  transition: $transition-time;
  overflow: hidden;
}

.tabs__content.tabs__content--active {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (max-width: $screen-md) {
  .tabs__buttons-wrapper {
    flex-direction: column;
    border-bottom: none;
  }
  .tabs__button,
  .tabs__button--link {
    padding: 10px 15px;
  }
}