// В этом файле должны быть стили для БЭМ-блока cart-item, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.cart-item {
  display: table-row;

  & > div {
    display: table-cell;
    border: 1px solid $gray-light;
    vertical-align: middle;
    text-align: center;
    padding: 14px;
    min-width: 120px;
    overflow: hidden;
  }
  &__image-cell img {
    max-width: 75px;
    max-height: 170px;
  }
  &__count {
    border: 1px solid #e2e2e2;
    font-size: 14px;
    padding: 10px 15px;
    max-width: 80px;
  }
  &__title {
    border-bottom: 1px solid;
    transition: $transition-time;
    line-height: 1.4;
  }
  &__title:hover {
    color: $red;
  }
  &__delete {
    font-size: 0;
    border: none;
    width: 30px;
    height: 30px;
    background: url(../img/cancel.svg);
    background-size: contain;
    cursor: pointer;
    transition: $transition-time;
  }
  &__delete:hover {
    background: url(../img/cancel-hover.svg);
    background-size: contain;
  }
}

@media (max-width: 991px) {
  div.cart-item {
    display: flex;
    flex-direction: column;
    
    & > div {
      min-width: 210px;
      border-right: none;
    }
    & > div:not(:last-child) {
      border-bottom: none;
    }

    &__image-cell {
      height: 130px;

      img {
        height: 100px;
      }
    }

    &__count-cell {
      height: 68px;
    }

    &__title-cell {
      height: 74px;
    }

    &__price-cell, &__sum-cell {
      height: 46px;
    }

    &__delete-cell {
      height: 50px;
      padding: 9px;
    }
  }
}