// В этом файле должны быть стили для БЭМ-блока form, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.form {

  margin-bottom: 20px;

  &__desc-block {
    margin: 0 0 30px;
    line-height: 1.3;
  }

  &__required-mark {
    color: $red;
  }

  &__field {

    &-wrap {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 5px;

      &--radios {
        padding: 0;
      }

      & input, & textarea, & select {
        max-width: 400px;
        font-size: 16px;
        padding: 8px;
        border: 1px solid $gray-light;
        font-family: Arial, sans-serif;
      }
    }

    &-block {
      display: flex;
      flex-flow: row nowrap;
      margin-bottom: 5px;

      &--horizontal {
        flex-direction: row;
      }

      &--vertical {
        flex-direction: column;
      }
    }

    &-radio-line {
      padding: 2px 0;
    }

    &-desc {
      color: $gray;
    }
  }

  &__label {
    &-wrap {
      min-width: 200px;
      padding-left: 5px;
      padding-bottom: 5px;
      padding-top: 5px;
    }
  }

  &__attraction {
    margin-top: 20px;
    padding: 10px 15px;
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #EDEDED;

    a {
      color: $red;
      border-bottom: 1px solid;
    }
  }

  &__buttons {
    padding: 5px;
  }

  &__submit {

    &:after {
      content: "";
      display: block;
      background-image: url(../img/arrow-form.svg);
      background-repeat: no-repeat;
      background-size: auto;
      width: 22px;
      height: 36px;
      position: absolute;
      right: -24px;
      top: -2px;
    }

  }
  &__submit::after, &__refresh::after {
    height: 36px;
  }

}

@media (max-width: 769px) {

  .form {
    &__field {
      &-block {
        flex-direction: column;
        padding-bottom: 10px;
      }
      &-desc {
        font-size: 12px;
      }
    }
    &__label-wrap {
      width: 100%;
    }
    &__attraction a {
      display: inline-block;
    }
    &__submit, &__refresh {
      margin: 0;
      margin-bottom: 10px;
      margin-right: 10px;

    }
  }
}

@media (max-width: 420px) {
  .form__buttons {
    text-align: center;
  }
}