// В этом файле должны быть стили для БЭМ-блока banners-slider, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.banners-slider {
  background: $yellow-mutted;
  max-height: 300px;

  .owl-nav {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    width: 100%;
    visibility: hidden;
  }

  .owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next, .owl-carousel .owl-dot {
    visibility: visible;
  }
  .owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next {
    font-size: 0;
    display: inline-block;
    width: 35px;
    height: 35px;
  }
  .owl-carousel .owl-nav .owl-prev {
    background: url(../img/slider-arrow-left.svg);
    background-size: contain;
  }
  .owl-carousel .owl-nav .owl-next {
    background: url(../img/slider-arrow-right.svg);
    background-size: contain;
  }
}

.banners-slider__item img {
  height: 300px;
  width: auto;
  object-fit: contain;
  max-width: 100%;
}

@media (max-width: 460px) {
  .banners-slider {
    .owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next {
      font-size: 0;
      display: inline-block;
      width: 35px;
      height: 35px;
      opacity: 0.3;
      transition: $transition-time;
    }
    .owl-carousel .owl-nav .owl-prev:hover, .owl-carousel .owl-nav .owl-next:hover {
      opacity: 1;
    }
  }
}
