// В этом файле должны быть стили для БЭМ-блока category-preview, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.category-preview {
	margin-bottom: 40px;

	&__title {
		font-size: 20px;
		color: $black;
	    padding-bottom: 5px;
	    margin: 1em 0;
	    line-height: 1.4;
	
		& a {
		    border-bottom: 1px solid $black;

			&:hover {
			    color: $link-hover-color;
			    border-bottom: 1px solid $link-hover-color;
			    text-decoration: none;
			}

		}

	}

	&__desc {
	    font-size: 14px;
	    margin: 10px 0 25px;
	    color: #666;
		line-height: 1.4;
	}

	&__product {

		&-list {
			box-shadow: 0 0 27px 8px $gray-light;
			border: 1px solid $gray-light;
		    background-color: #f5f5f5;
		    width: 100%;
			display: flex;
		    flex-flow: row wrap;
		    padding: 10px;
		    justify-content: space-around;
		}

		&-item {
		    text-align: center;
		    font-size: 14px;
		    color: #3a3a3a;
		    width: 170px;
		    display: flex;
		    flex-direction: column;
		    margin: 10px 7px;

		    & a {
			    font-size: 14px;
			    color: #444;

			    & img {
				    height: 300px;
				    width: 100%;
				    max-width: 140px;
			    }

		    }

		}

		&-title {
			padding: 5px;
			line-height: 1.4;
		}

		&-price {
		    font-size: 14px;
		    padding: 5px 0;
		    display: inline-block;
			line-height: 1.4;
		    color: $black;
		}

	}

}

@media (max-width: 480px) {

	.category-preview__product-item {
		width: 100%;
		margin-bottom: 20px;
	}

	.category-preview__product-title {
		padding: 10px 0;
	}

	.category-preview__product-price {
		font-weight: bold;
	}

}

@media (min-width: 481px) and (max-width: 769px) {

	.category-preview__product-item {
		margin: 0;
		margin-bottom: 20px;
		width: 50%;
	}

}

