// В этом файле должны быть стили для БЭМ-блока modal, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.modal {
  position: fixed;
  background: rgba(0, 0, 0, 0.3);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 2;
  transition: z-index 0.5s step-start, opacity 0.5s linear;
}

.modal.modal--hidden {
  z-index: -1;
  opacity: 0;
  transition: z-index 0.5s step-end, opacity 0.5s linear;

  .modal__window {
    top: 50%;
    left: 50%;
    position: fixed;
    transform: translateX(-50%) translateY(-150%);
  }
}

.modal__window {
  position: fixed;
  background: #fff;
  border: 5px solid $gray-soft;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 800px;
  height: auto;
  min-height: 300px;
  max-height: 700px;
  overflow-y: scroll;
  padding: 40px 30px 20px;
  transition: 0.5s;
  font-size: 14px;
  line-height: 1.7;

  & p {
    margin: 1em 0;
  }

  & small {
    color: $gray-mutted;
    font-size: 12px;
  }
}

.modal__window::-webkit-scrollbar {
  width: 0;
}

.modal__title {
  font-size: 20px;
  margin: 0 0 20px;
  color: $gray-mutted;
  font-family: Arial, sans-serif;
}

.modal__close {
  position: absolute;
  right: 11px;
  top: 11px;
  font-size: 0;
  width: 28px;
  height: 28px;
  border: none;
  background: url('../img/modal-cancel-gray.svg');
  background-size: contain;
  cursor: pointer;
  transition: $transition-time;
}

.modal__close:hover {
  background: url('../img/modal-cancel-black.svg');
  background-size: contain;
}

.modal__overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  cursor: pointer;
}

@media (max-width: $screen-md) {
  .modal__title {
    text-align: center;
    line-height: 1.4;
  }
}