// В этом файле должны быть стили для БЭМ-блока aside, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.aside {
    min-width: 240px;
    padding-left: 10px;
    margin-bottom: 20px;
}

.aside__actions-item {
	display: inline-block;
	color: #000;
	text-decoration: underline;
	margin: 20px 0 20px 45px;
	position: relative;
    font-size: 21px;
	font-weight: normal;
}

.aside__actions-item:hover {
	text-decoration: none;
}

.aside__actions-item:before {
	content: "";
	position: absolute;
	top: 0;
    right: 86px;
	display: inline-block;
	width: 24px;
	height: 24px;
    background-image: url(../img/a_icon.svg);
    background-size: 100%;
    background-position: left;
    background-repeat: no-repeat;
}

.aside__menu-title {
	display: block;
	margin: 20px 0;
    font-size: 20px;
}

.aside__menu-title--first {
	margin-top: 0;
}

.aside__submenu-item {
	padding: 12px 0;
    border-bottom: 1px dotted #ddd;
}

.aside__submenu-item a {
	display: block;
    font-size: 14px;
    padding-left: 15px;
}

.aside__submenu-item a:hover {
	text-decoration: underline;
    color: #403d30;
}

@media (max-width: 769px) {
	
	.aside {
		display: none;
	}

}

