// В этом файле должны быть стили для БЭМ-блока catalog, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog {
	color: $gray;
	font-size: 14px;
	margin-top: 40px;
	line-height: 1.4;
}

// catalog__top //

.catalog__top {
	flex-flow: row wrap;
    border-bottom: 1px solid #e9e9e9;
    border-top: 1px solid #e9e9e9;
    padding: 15px 0;
    cursor: default;
}

.catalog__sort, .catalog__pagination {
	width: 50%;
}

.catalog__sort-devider {
	display: inline-block;
	padding: 0 5px;
}

// catalog__main //

.catalog__main {
	padding: 10px 0;
	flex-flow: row wrap;
}

// catalog__bottom //

.catalog__bottom {
	justify-content: flex-end;
    border-bottom: 1px solid $gray-light;
    border-top: 1px solid $gray-light;
    margin-bottom: 10px;
	padding: 15px 0;
}

@media (max-width: 769px) {
	.catalog__bottom {
		align-items: center;
	}
}

@media (max-width: 1122px) {
	.catalog__top,
	.catalog__bottom {
		flex-direction: column;
		align-items: center;
	}
	.catalog__sort, .catalog__pagination {
		width: 100%;
		justify-content: center;
	}
	.catalog__sort {
		padding-bottom: 15px;
		border-bottom: 1px solid $gray-light;
		margin-bottom: 15px;
	}
}

@media (min-width: 320px) and (max-width: 1142px) {
	.catalog__main {
	    justify-content: center;
	}
}

@media (max-width: 1020px) {
	.catalog__top, .catalog__bottom {
		padding: 15px 0 0;
	}
}