// В этом файле должны быть стили для БЭМ-блока filter, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.filter__form {
  padding: 10px;
  max-width: 500px;
}

.filter__field {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 10px;

  label {
    color: $gray;
    font-size: 14px;
    min-width: 150px;
  }

  select, option, input {
    padding: 8px 10px;
    font-size: 14px;
    border: 1px solid $gray-light;
    display: block;
    flex-grow: 1;
  }

  &--select::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 10px solid $gray-light;
    pointer-events: none;
  }
}

.filter__colors-item {
  position: relative;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  padding: 1px 5px;

  input {
    visibility: hidden;
    height: 0;
  }

  label::before {
    content: '';
    width: 18px;
    height: 18px;
    border-radius: 3px;
    border: 1px solid $gray-light;
    position: absolute;
    left: 0px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }

  input:checked + label::before {
    content: '';
    width: 18px;
    height: 18px;
    border-radius: 3px;
    border: 1px solid $gray-light;
    position: absolute;
    left: 0px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    background: url(../img/filter-tick-red.svg);
    background-size: contain;
  }
}

.filter__colors {
  margin: 15px 0 20px;
}

.filter__colors-list {
  column-count: 2;
  column-gap: 30px;
}

.filter__color-name {
  font-size: 14px;
}

.filter__text-range {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.filter__text-range-title {
  color: $gray;
  font-size: 14px;
  min-width: 150px;
  margin-bottom: 5px;
}

.filter__text-range-fields {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  flex-grow: 1;
}

.filter__text-range-field {
  display: flex;
  align-items: center;
  margin-right: 8px;
  flex-grow: 1;

  label {
    color: $gray;
    font-size: 14px;
    margin-right: 5px;
  }

  input {
    padding: 8px 10px;
    font-size: 14px;
    border: 1px solid $gray-light;
    display: block;
    flex-grow: 1;
  }
}

.filter__submit-btn {
  @include button--red;
  text-transform: uppercase;
  letter-spacing: 0.2px;
}

@media (max-width: 550px) {
  .filter__colors-item {
    margin-bottom: 5px;
    padding: 5px 7px;

    label::before {
      width: 22px;
      height: 22px;
    }
    input:checked + label::before {
      width: 22px;
      height: 22px;
    }
  }
}

@media (max-width: 500px) {
  .filter__field {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    label {
      margin-bottom: 5px;
    }

    select, option, input {
      width: 100%;
    }

    &--select::after {
      top: 36px;
    }
  }
  .filter__colors-list {
    display: flex;
    flex-direction: column;
    column-count: 1;
  }
  .filter__text-range-fields {
    flex-wrap: wrap;
  }
  .filter__text-range-field {
    margin-right: 0;
    margin-bottom: 7px;
  }
}


