*, *:before, *:after {
  box-sizing: border-box;
}

body {
  font-family: Verdana, sans-serif;
  font-size: 16px;
  color: $text-color;
  background: $body-bg;
  min-height: 100vh;
  color: $black;
  min-width: 320px;
}

a {
  color: $black;
}

.clearfix {
  content: "";
  display: table;
  clear: both;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
  line-height: 1em;
  cursor: pointer;
  /* for FF */
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: '';
  /* for IE */
  -ms-appearance: none;
  appearance: none!important;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  width: 0;
  height: 0;
}

.hidden {
  display: none !important;
}

.flex {
  display: flex;
  flex-flow: row nowrap;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}

.container--flex {
  display: flex;
  flex-flow: row nowrap;
}

.page__content {
  margin-left: 35px;
  margin-bottom: 30px;
}

.page__title {
  font-size: 29px;
  margin: 1em 0;
  line-height: 1.2;
}

.page__title--red {
  color: $red;
}

.button {
  padding: 7px 20px;
  border: 2px solid $yellow;
  background-color: $yellow;
  cursor: pointer;
  position: relative;
  margin-right: 14px;
  min-width: 172px;
  font-size: 16px;

  &--gray {
    background: $gray-light;
    border: 2px solid $gray-light;

  }

  &--red {
    background: $red;
    color: $white;
    border: none;
  }

  &:after {
    content: "";
    display: block;
    background-image: url(../img/button-right-arrow.svg);
    background-repeat: no-repeat;
    background-size: auto;
    width: 22px;
    height: 34px;
    position: absolute;
    right: -24px;
    top: -2px;
  }

  &--gray:after {
    content: "";
    display: block;
    background-image: url(../img/button-right-arrow--gray.svg);
    background-repeat: no-repeat;
    background-size: auto;
    width: 22px;
    height: 34px;
    position: absolute;
    right: -24px;
    top: -2px;
  }
}

@media (max-width: 769px) {
  .page__content {
    margin-left: 0;
  }
}

@media (max-width: 480px) {
  .container--flex {
    flex-flow: column nowrap;
  }
}
