// В этом файле должны быть стили для БЭМ-блока catalog-item, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog-item {
  display: flex;
  flex-basis: 45%;
  margin: 40px 0;
  min-width: 390px;

  &:nth-child(even) {
    margin-left: 40px;
  }

  &__image-wrap {
    margin-right: 20px;
  }

  &__image-wrap img {
    width: 120px;
    box-shadow: 0 8px 4px rgba(0, 0, 0, 0.3);
  }

  &__description {

    & h3 {
      font-size: 15px;
      margin-bottom: 15px;

      & a {
        text-decoration: underline;
      }

      & a:hover {
        text-decoration: none;
      }

    }

  }

  &__in-stock {
    border-bottom: 1px dotted $gray-light;

    img {
      width: 180px
    }

  }

  &__info {

    &-line {
      display: flex;
      flex-flow: row nowrap;
      border-bottom: 1px dotted $gray-light;
      align-items: center;

      & span {
        width: 50%;
        padding: 8px;
      }

    }

    &-value {
      color: $black;
    }

    &-price {
      font-size: 22px;
    }

  }

}

.catalog-item--sale {

  & .catalog-item__image-wrap {
    position: relative;

    & .catalog-item__label {
      position: absolute;
      top: 0;
      left: 0;
      width: 54px;
      height: 54px;
      box-shadow: none;
      transform: translate(-50%, -50%);

      &:nth-child(2) {
        top: 60px;
      }

      &:nth-child(3) {
        top: 120px;
      }
    }
  }

}

@media (min-width: 320px) and (max-width: 1142px) {
  .catalog-item {

    &:nth-child(even) {
      margin-left: 10px;
    }

  }

}

@media (max-width: 479px) {

  .catalog-item {
    min-width: auto;
    margin-bottom: 50px;
    flex-flow: column wrap;
    flex-basis: auto;
    align-items: center;
    margin-left: 0 !important;

    &__description {
      margin-top: 15px;
    }

  }

}


