// В этом файле должны быть стили для БЭМ-блока catalog-leaders, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.catalog-leaders {
		font-size: 	15px;
	    display: flex;
	    flex-flow: row wrap;

	&__item {
		display: flex;
		min-width: 390px;
		margin: 10px 0 30px 0;
	    width: auto;

		&:nth-child(odd) {
			margin-right: 20px;
		}

		&-image-wrap {
			margin: 0 20px 10px 0;

				& img {
				width: 120px;
			    border: 1px solid #e2e2e2;
			    padding: 2px;
			}

		} 

		&-info {
			
			&-line {
				display: flex;
				flex-flow: row nowrap;
			    border-bottom: 1px dotted $gray-light;
			    align-items: center;
				line-height: 1.2;

				& span {
					padding: 8px;
					max-width: 150px;

					&:first-child {
						width: 60%;
						padding-left: 0;
					}

					&:last-child {
						width: 100%;
					}

				}

			}

		}

		&-title {
			margin-bottom: 	20px;
			max-width: 230px;
			line-height: 1.2;

			& a {
				text-decoration: underline;
			}

			& a:hover {
				text-decoration: none;
			}
			
		}

	}

}

@media (max-width: 480px) {

	.catalog-leaders {
		justify-content: center;

		&__item {
			flex-direction: column;
			align-items: center;
		    min-width: stretch;

			&-info {
				width: 100%;

				&-line {
					border: none;

					&:last-child {
						border-bottom: 1px solid $gray-light;
					}
				}
			}

			&-image-wrap {
				margin: 0 auto 10px;
			}

			&-info-line span:first-child, &-info-line span:last-child {
				width: 50%;
				border: 1px solid $gray-light;
				border-bottom: 0;
				align-self: stretch;
				padding: 8px;
				overflow: hidden;
				max-width: 100%;
			}

			&-title {
				text-align: center;
				margin: 0 auto 20px;
			}

			&:nth-child(odd) {
				margin-right: 0;
			}
		}

	}

}
