// В этом файле должны быть стили для БЭМ-блока footer, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.footer {
    padding-top: 15px;
    padding-bottom: 10px;
    background-color: #4c463b;
    width: 100%;
    font-size: 12px;

    &__top {
    	color: #fff;
    	margin: 1em;
    	display: flex;
    	justify-content: space-between;
    	line-height: 1.7;
    	width: 100%;
    	flex-flow: row nowrap;
        font-size: 14px;

    	& div {
			padding: 10px;
    	}

    	& a {
    		color: #fff;
    	}
    	
    }

    &__rules-text {
    	margin: 1em;
	    color: #999;
	    line-height: 1.4;

	    & p {
	    	padding: 10px;
	    }
    }

    &__social {
    	margin: 0 1em;

    	& a {
    		padding: 10px;
    	}
    }

}

.footer .container {
	display: flex;
	flex-flow: row wrap;
}

@media (max-width: 480px) {

    .footer__top {
        font-size: 12px;
    }
	
	.footer__rules-text {
		display: none;
	}

	.footer__social img {
		width: 100%;
	}

}


@media (max-width: 828px) {

	.footer {

		&__top {
			flex-flow: column nowrap;
			align-items: center;
			text-align: center;

		}

	    &__social {
			width: 100%;

	    	& a {
	    		margin: 0 auto;
	    	}
	    }

	}

}