// В этом файле должны быть стили для БЭМ-блока product-item, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.product-item {

  &__title {
    font-size: 29px;
    margin: 1em 0;
    line-height: 1.2;
  }

  &__main {
    display: flex;
  }

  &__main-picture-block {
    width: 300px;
    margin-bottom: 2px;
  }

  &__secondary-pictures-block {
    display: flex;
    flex-wrap: wrap;

    a, a img {
      padding: 0px 3px 2px 0;
      outline: none;
    }
  }

  &__secondary-pictures-block img {
    width: 112px;
  }

  &__pictures-increase-message {
    color: $gray-mutted;
    font-size: 14px;
    padding: 8px 0;
    display: inline-block;
    position: relative;
    padding-left: 18px;

    &::before {
      content: '';
      background: url(../img/fancy-loop.gif);
      background-size: contain;
      position: absolute;
      left: 0px;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      width: 12px;
      height: 12px;
    }
  }
  &__characteristics-line {
    display: flex;
    font-size: 14px;
    border-bottom: 1px solid $gray-light;
  }

  &__characteristics-key, &__characteristics-value {
    line-height: 1.3;
    padding: 15px 0;
  }

  &__characteristics-key {
    min-width: 200px;
    max-width: 200px;
    align-self: center;
  }

  &__characteristics-value {
    flex-grow: 1;
    max-width: 350px;

    a {
      border-bottom: 1px solid;
      color: $red;

      &:hover {
        color: #000;
      }
    }
  }

  &__characteristics-fieldset {
    line-height: 1.3;
    padding: 10px 0;
    flex-grow: 1;
    display: flex;

    select, input, option {
      font-size: 14px;
      border: 1px solid $gray-light;
      align-self: center;
    }

    input {
      padding: 8px 10px;
      max-width: 70px;
      margin-left: 10px;
    }

    select {
      padding: 10px;
      width: 100%;
    }
  }

  &__characteristics-select {
    position: relative;
    flex-grow: 1;
    align-self: center;
  }

  &__characteristics-select::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 10px solid $gray-light;
    pointer-events: none;
  }

  &__modal-links {
    display: flex;
    margin-top: 30px;
  }

  &__modal-link {
    position: relative;
    padding-left: 38px;
    line-height: 1.4;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      color: $red;
    }
  }

  &__modal-link::before {
    content: '';
    position: static;
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    left: 0;
    top: 12px;
  }

  &__modal-link--delivery::before {
    background: url(../img/modal-delivery-icon.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 28px;
    height: 32px;
  }

  &__modal-link--measurement::before {
    background: url(../img/modal-measure-icon.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 29px;
    height: 24px;
  }

  &__modal-link--howorder::before {
    background: url(../img/modal-cart-icon.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 29px;
    height: 24px;
  }

  &__modal-link--payways::before {
    background: url(../img/modal-card-icon.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 29px;
    height: 24px;
  }

  &__buttons {
    display: flex;
    margin-top: 30px;

    .button::after {
      height: 46px;
    }

    .product-item__buy-btn {
      margin-left: -20px;
      padding-left: 30px;
      display: flex;
      align-items: center;

      input {
        font-size: 14px;
        border: 1px solid $gray-light;
        align-self: center;
        padding: 5px 10px;
        max-width: 60px;
      }

      button {
        background: none;
        border: none;
        font-size: 20px;
        text-transform: uppercase;
        margin-left: 10px;
        cursor: pointer;
        outline: none;
      }
    }

    .price__label {
      z-index: 2;
      display: flex;
      align-items: center;
      font-size: 22px;
      justify-content: center;
      font-family: Arial, sans-serif;
    }
  }
  &__colors-block {
    margin-top: 35px;
    color: $red;
    font-size: 19px;

    &-title {
      line-height: 1.4;
    }
  }

  &__colors-list {
    display: flex;
    flex-wrap: wrap;
    padding: 15px 0;
  }

  &__color {
    margin-right: 15px;
    margin-bottom: 5px;
    width: 100px;
  }
}

@media (max-width: 1280px) {
  .product-item__modal-links {
    flex-wrap: wrap;
  }
  .product-item__modal-link {
    width: calc(50% - 15px);
    margin-right: 15px;
    padding: 12px 0 4px 38px;
  }
  .product-item__characteristics-line {
    flex-wrap: wrap;
    min-height: 48px;
    padding: 12px 0;
  }
  .product-item__characteristics-key, .product-item__characteristics-value {
    padding: 0;
    max-width: 100%;
  }
  .product-item__characteristics-key {
    font-size: 12px;
    font-weight: bold;
  }
}

@media (max-width: 1024px) {
  .product-item__main {
    flex-direction: column;
    justify-content: center;
  }
  .product-item__characteristics-key {
    max-width: 200px;
  }
  .product-item__main-picture {
    height: 400px;

    &-block {
      width: auto;
      margin-right: 5px;
    }
  }
  .product-item__secondary-pictures-block {
    flex-direction: column;
  }
  .product-item__pics-section {
    display: flex;
    flex-wrap: wrap;
  }
  .product-item__pictures-increase-message {
    width: 100%;
  }
}

@media (max-width: 420px) {
  .product-item__buttons {
    flex-direction: column;
  }
  .product-item__buttons .price__label {
    flex-grow: 1;
  }
  .product-item__buttons .product-item__buy-btn {
    flex-grow: 1;
    margin-left: 0;
    justify-content: center;
  }
  .product-item__modal-links {
    flex-direction: column;
  }
  span.product-item__modal-link {
    width: 100%;
  }
}