// В этом файле должны быть стили для БЭМ-блока sort, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.sort {
  align-items: center;
}

.sort__block {
  white-space: nowrap;
  margin-right: 14px;
  display: inline-block;
}

.sort__block-name {
  padding-right: 8px;
}

.sort__btn-asc,
.sort__btn-desc {
  font-size: 0;
  color: $white;
  text-decoration: none;
  background-color: $gray-mutted;
  border: none;
  width: 27px;
  height: 27px;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  transition: $transition-time;
  border-radius: 50%;
}

.sort__btn-asc {
  background-image: url(../img/arrow-up-white.svg);
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center;
}

.sort__btn-desc {
  background-image: url(../img/arrow-down-white.svg);
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center;
}

.sort__btn-asc:hover,
.sort__btn-desc:hover {
  background-color: $red;
}

@media (max-width: 768px) {
  .sort__block {
    width: 175px;
    margin-right: 10px;
  }
}

@media (max-width: 420px) {
  section.sort {
    flex-direction: column;
    padding-bottom: 5px;
  }
  .sort__block-name {
    display: inline-block;
    margin-bottom: 15px;
    padding-right: 8px;
  }
}



