@font-face {
  font-family: "Verdana";
  src: url("../fonts/verdana.eot"); /* IE9*/
  src: url("../fonts/verdana.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
  url("../fonts/verdana.woff2") format("woff2"), /* chrome、firefox */
  url("../fonts/verdana.woff") format("woff"), /* chrome、firefox */
  url("../fonts/verdana.ttf") format("truetype"), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url("../fonts/verdana.svg#Verdana V1 V2") format("svg"); /* iOS 4.1- */
}

@font-face {
  font-family: "Verdana";
  font-weight: bold;
  src: url("../fonts/verdana-bold.eot"); /* IE9*/
  src: url("../fonts/verdana-bold.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
  url("../fonts/verdana-bold.woff") format("woff"), /* chrome、firefox */
  url("../fonts/verdana-bold.ttf") format("truetype") /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
}