// В этом файле должны быть стили для БЭМ-блока article, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
.article {

	p, span, li, i, em, strong {
		font-size: 14px;
		line-height: 1.7;
		vertical-align: top;
		color: $gray-dark;
		margin-bottom: 10px;
	}

	small {
		font-size: 12px;
		color: $gray-mutted;
	}

	h1 {
		font-size: 29px;
		margin: 1em 0;
	}

	h2 {
	    color: #c00;
	    font-size: 21px;
		margin: 25px 0 15px;
	}

	h3 {
		margin: 1em 0;
		font-size: 18px;
	}

	ul {
		margin: 1em 0;
		padding-left: 40px;
		list-style-type: disc;
	}

	ul p,
	ul span {
		margin: 0;
	}

	a {
		text-decoration: underline;

		&:hover {
			color: $red;
		}
	}

	img {
		margin: 0.5em 0;
		width: auto;
		max-width: 100%;
	}

	table {
		border-collapse: collapse;
		td {
			padding: 5px 15px;
			border: 1px solid $gray-light;
			overflow: hidden;
		}
		td:nth-child(odd) {
			background: #f7f7f7;
		}
		td:not(:first-child) {
			min-width: 72px;
		}
	}

	&__table-wrapper {
		margin: 10px 0 15px;
		overflow-x: auto;
	}

	&__title--border {
	    padding-bottom: 5px;
	}

	&__body {
		transition: max-height 1s ease;
	    overflow: hidden;
	    position: relative;
	}

	&__more {

		&-less-toggler {
		    color: #cc0000;
		    font-size: 14px;
		    margin-top: 10px;
		    padding: 7px 20px;
		    text-align: center;
		    border: 1px solid #e2e2e2;
		    background-color: #eee;
		    border-radius: 3px;
		    transition: all 0.5s;
		    cursor: pointer;
		    display: none;

		    &:hover {
			    border: 1px solid #eee;
			    color: #777;
		    }

		}

	}

}

.bottom-shadow {
    background-attachment: scroll;
    background-clip: border-box;
    background-color: rgba(0, 0, 0, 0);
    background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#fff));
    background-image: -webkit-linear-gradient(top, transparent, #fff);
    background-image: -o-linear-gradient(top, transparent, #fff);
    background-image: linear-gradient(to bottom, transparent, #fff);
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
    background-origin: padding-box;
    background-position-x: 0;
    background-position-y: 0;
    background-repeat: repeat;
    background-size: auto auto;
    bottom: 0;
    display: block;
    height: 100px;
    position: absolute;
    width: 100%;
    margin: 0 !important;
}

.turn-off {
	position: relative;

	& .article__body {
		max-height: 130px;
		& .bottom-shadow {
			visibility: visible;
		}
		
	}

	& .article__more-less-toggler--on {
		display: block;
		position: relative;
		top: -10px;
	}

}

.turn-on {

	& .article__body {
		max-height: 100%;

		& .bottom-shadow {
			visibility: hidden;
		}

	}

	& .article__more-less-toggler--off {
		display: block;
	}

	& .bottom-shadow {
		width: auto;
	}

}