// В этом файле должны быть стили для БЭМ-блока breadcrumbs, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.breadcrumbs {
  margin-top: 20px;
  padding-right: 25px;
  font-size: 14px;
}

.breadcrumbs__list {

  padding: 10px 0 10px 25px;
  box-shadow: inset 0 2px #E7E7E7, inset 0 -2px #E7E7E7;
  position: relative;
  display: inline-block;

  li {
    padding: 5px 0;
    padding-left: 10px;
    display: inline-block;
  }

  li a {
    text-decoration: underline;
  }

  li a:hover {
    color: $link-hover-color;
    text-decoration: none;
  }

  li:after {
    content: "/";
    padding-left: 13px;
  }

  li:last-child {
    padding-right: 20px;
  }

  li:last-child a {
    text-decoration: none;
    pointer-events: none;
  }

  li:last-child a:hover {
    cursor-event: none;
    color: inherit;
  }

  li:last-child:after {
    content: "";
  }

  &:after {
    content: "";
    position: absolute;
    display: block;
    height: 100%;
    width: 40px;
    background: url(../img/breadcrumbs-arrow.svg);
    background-size: auto;
    background-repeat: no-repeat;
    top: 0;
    right: -40px;
  }

  &:before {
    content: "";
    position: absolute;
    display: block;
    height: 100%;
    width: 40px;
    background: url(../img/breadcrumbs-arrow.svg);
    background-size: auto;
    background-repeat: no-repeat;
    top: 0;
    left: 0;
  }
}

@media (max-width: $screen-md) {
  .breadcrumbs {
    margin-top: 0;
  }
}

@media (max-width: 490px) {

  .breadcrumbs__list {
    text-align: center;

    li {
      padding: 5px;
    }
    li:last-child {
      padding-bottom: 0;
    }
  }
}