// В этом файле должны быть стили для БЭМ-блока news-list, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.news-list {
	p,
	span {
	    font-size: 13px;
	    line-height: 1.7;
	    color: $gray-dark;
	    margin-bottom: 15px;
	}

	h2 {
	    font-weight: bold;
	    font-size: 13px;
	    text-transform: uppercase;
	    margin: 0 0 15px 0;
	    line-height: 1.7;
	}

	 img {
	    max-width: 100%;
	    max-height: 275px;
	}

	&__item {
		margin-bottom: 30px;
		display: flex;
	    justify-content: space-between;

	    &-image-wrap {
		    max-width: 30%;
		}

		&-body {
			display: flex;
			flex-flow: column wrap;
		    justify-content: center;
		    align-items: flex-start;
		}

		&-title a {
		    text-decoration: none;
		    color: $black;
			font-size: 14px;

		    &:hover {
			    color: $link-hover-color;
				border-bottom: 1px solid;
		    }

		}

	}

	&__item:first-child {
		margin-top: 30px;
	}

	&__timestamp {
	    margin-right: 25px;
	    align-self: flex-end;
	    font-style: italic;
	}

}

@media (max-width: 769px) {

	.news-item__timestamp {
		margin: 0;
	}

	.news-list__item-body {
		order: 2;
	}

	.news-list__item-image-wrap {
		display: flex;
		align-items: center;
		margin-bottom: 10px;
	}

}

@media (max-width: 480px) {

	.news-list {

		&__item {
		    flex-direction: column;
		    align-items: center;

		}

	}

}
