// В этом файле должны быть стили для БЭМ-блока search, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.search {
  box-shadow: 0 7px 18px -6px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  margin-bottom: 16px;

  & .container {
    padding: 15px 10px;
  }

  &__choose-maker-btn-holder {
    margin-right: 10px;

    & button {
      font-size: 14px;
      min-width: 220px;
      border: 3px solid #D2393E;
      background-color: #D2393E;
      padding: 5px 20px;
      color: #fff;
      cursor: pointer;
      transition: background-color 0.5s ease, border 0.5s ease;

      &:hover {
        border: 3px solid #666;
        background-color: #666;
      }

    }
  }

  &__form-wrapper {
    width: 100%;
  }

  &__form {
    display: flex;
    justify-content: space-around;

    &-field {
      width: 100%;
      flex-grow: 1;
      flex-shrink: 1;
      color: #222;
      margin-right: 10px;
      padding: 6px 10px;
      border: 3px solid $yellow;
    }

    &-submit.button {
      font-size: 14px;
    }
  }

  &__choose-maker-list {
    column-count: 3;
    font-size: 14px;
    line-height: 1.3;
    &-item {
      padding: 7px 0;
      margin-bottom: 5px;
      list-style-type: circle;
      list-style-position: inside;
      border-bottom: 1px dotted $gray-light;
      &:first-of-type {
        border-top: 1px dotted $gray-light;
      }
      a:hover {
        color: $red;
      }
    }
  }
}

@media (max-width: 940px) {
  .search__choose-maker-list {
    column-count: 2;
    column-gap: 25px;
  }
  .search__choose-maker-list-item:first-of-type {
    border-top: none;
  }
}

@media (max-width: 769px) {
  .search {
    box-shadow: none;
    max-width: 500px;
    margin: 0 auto;
  }
  .search__choose-maker-list {
    column-count: 1;
  }
  .search__choose-maker-btn-holder {
    display: none;
  }
}

@media (max-width: 480px) {
  .search__form-submit.button {
    min-width: 70px;
    font-size: 0;
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30%;
    background-position-y: center;
  }

}

