// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.header__top {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  margin-bottom: 20px;
}

.header__top > div {
  align-self: center;
}

.header__brand-block {
  display: flex;
  align-items: center;
  flex-grow: 1;
  max-width: 720px;
}

.header__brand-block a {
  display: flex;
  align-items: center;
}

.header__brand-block h1 {
  color: $red;
  margin: 0;
  font-size: 20px;
  line-height: 22px;
  width: 180px;
  text-transform: uppercase;
  padding-left: 20px;
  position: relative;
}

.header__brand-block h1::after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: -18px;
  transform: translateY(-50%);
  width: 10px;
  height: 38px;
  background: url(../img/header__brand-block-arrow.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.header__brand-slogan {
  padding-left: 40px;
}

.header__slogan-desc {
  font-size: 12px;
  line-height: 1.2;
  color: $gray-mutted;
  font-style: normal;
  min-width: 160px;
  max-width: 180px;
  display: block;
  margin-right: 30px;
}

.header__cart {
  flex-grow: 1;
  //max-width: 250px;
}

.header__cart-label {
  border: 2px solid $yellow;
  border-right: none;
  padding: 5px 7px;
  position: relative;
  margin-right: 30px;
  min-width: 220px;
  display: flex;
  justify-content: center;
}

.header__cart-label:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: -32px;
  transform: translateY(-50%);
  width: 33px;
  height: 108%;
  background: url(../img/header__cart-label-arrow.svg);
  background-size: contain;
  background-position: left;
  background-repeat: no-repeat;
}

.header__cart-icon {
  background: url(../img/cart.svg);
  background-size: contain;
  background-position: left;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
}

.header__cart-info {
  padding-left: 10px;
  align-self: center;
  font-size: 14px;
  line-height: 1.4;
}

.header__goods-number, .header__goods-price {
  white-space: nowrap;
}

.header__phones-list {
  display: flex;
  flex-direction: column;
}

.header__contacts {
  text-align: right;
}

.header__when-call {
  font-size: 14px;
  color: $gray;
  margin-bottom: 5px;
  display: block;
  white-space: nowrap;
}

.header__phones-item {
  font-size: 18px;
  line-height: 1.2;
  font-family: Arial, sans-serif;
}

.header__phones-item:hover {
  color: $red;
}

.header__phones-item .decorated {
  font-weight: bold;
  font-size: 20px;
}

.header__callback-btn-holder {
  text-align: right;
  font-size: 14px;
  text-decoration: underline;
  display: inline-block;
  padding-top: 5px;
}

.header__callback-btn-holder a:hover {
  color: $red;
}

@media (max-width: $screen-lg) {
  .header__brand-block {
    flex-direction: column;
    align-items: flex-start;
    flex-grow: 0;
  }
  .header__brand-slogan {
    padding-left: 15px;
    align-self: flex-start;
    padding-top: 8px;
  }
  .header__slogan-desc {
    max-width: 240px;
  }
  div.header__cart {
    margin: 0;
    margin-left: 27px;
  }
  .header__cart-label {
    justify-content: center;
  }
  .header__brand-block h1::after {
    display: none;
  }
}

@media (max-width: 749px) {
  .header__top {
    flex-wrap: wrap;
    justify-content: center;
  }
  div.header__cart {
    max-width: 470px;
    order: 3;
    margin-left: 30px;
    padding-top: 10px;
  }
}

@media (max-width: 524px) {
  .header__top {
    flex-direction: column;
    align-items: center;
  }
  .header__callback-btn-holder {
    padding-top: 10px;
  }
  .header__phones-item {
    line-height: 1.4;
  }
  div.header__cart {
    order: 3;
    margin: 0 auto;
    padding-top: 8px;
  }
  .header__contacts {
    margin: 24px 0 10px;
    text-align: center;
  }
}


